.pag__table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
    border: 2px solid black;
}


.pag__table th{
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #7fffd4;
    color: black;
    text-transform: uppercase;
}
.pag__table button.active {
    background-color: #7fffd4;
    color: white;
}
