
#signup {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#signup .signupform {
    background-color: #7fffd4;
    width: 370px;
    padding: 38px 55px 33px;
    border: 2px solid black;
}

#signup img {
    width: 70px;
    text-align: center;
    display: flex;
    margin: 0 auto;
    margin-bottom: 50px;
}

#signup label {
    width: 150px;
}

#signup h2 {
    text-align: center;
    font-family: system-ui;
    font-size: 30px;
    font-weight: 600;
}


#signup input {
    width: 300px;
    height: 26px;
    padding: 6px;
    margin-bottom: 10px;
}

#signup .formbox {
    font-family: system-ui;
    font-size: 15px;
    color: #555;
    display: flex;
    align-items: center;
    column-gap: 15px;
    margin-bottom: 10px;
}

#signup button {
    margin-left: 130px;
    height: 50px;
    text-transform: uppercase;
    width: 25%;
    border: none;
    color: black;
    font-size: 15px;
    margin-top: 10px;
    cursor: pointer;
}
