 #page card {
    background-color: #7fffd4;
    display: inline-block;
    width: 220px;
    height: 340px;
    padding: 15px;
    margin-left: 30px;
    margin-top: 35px;
   
    border: 2px solid #7fffd4;
    border-radius: 6px;
    /* box-shadow: 5px 5px 5px 5px #ec407a; */
    position: absolute;
    top: 80;
  }
  #page button {
    width: 50px;
    height: 20px;
    background-color: darkseagreen;
    /* color: #fff; */
    margin-left: 80px;
    margin-top: 2px;
    border: none;
  }
  #page button:hover {
    background-color: black;
    color: white;
    transition: 2s;
  }
  #page table {
    background-color: #7fffd4;
    /* width: 550px; */
    /* height: 300px; */
    margin-left: 300px;
    /* margin-top: 75px; */
    /* margin-bottom: 25px; */
    
    text-align: center;
    position: absolute;
    top:226px;

  }